import React from 'react';
import { functionName, functionParams, functionReturn } from './FunctionName.module.css';

export default ({ children, parameters, returns }) => {
    const params = parameters ? ` ${parameters.join(', ')} ` : ' ';
    return <label>
        <strong className={functionName}>{children}</strong>(<span className={functionParams}>{params}</span>)
        {returns &&
            <span className={functionReturn}>
                {` -> {${returns}}`}
            </span>
        }
    </label>
};