import React from 'react';
import Table from 'react-bootstrap/Table';
import { table } from './ParameterTable.module.css';
import classnames from 'classnames';

export default ({ children, withDefaultColumn = false }) => (
    <Table size='sm' className={classnames('table-borderless', table)}>
        <thead>
            <tr>
                <th>Name</th>
                <th>Type</th>
                {withDefaultColumn && <th>Default</th>}
                <th>Description</th>
            </tr>
        </thead>
        <tbody>
            {children}
        </tbody>
    </Table>
);