import React from 'react';
import DefaultLayout from './DefaultLayout';
import { sideMenu, main } from './APILayout.module.css';
import classnames from 'classnames';
import { Link } from 'gatsby';

const NavLink = ({ to, children }) => (
    <Link to={to} className='nav-link text-white'>{children}</Link>
);

export default ({ children }) => (
    <DefaultLayout>
        <div className={classnames('p-2', sideMenu)}>
            <nav className="nav flex-column" style={{ color: 'white ' }}>
                <NavLink to='/apidoc/layer/GeoserverLayer'>easy/layer/GeoserverLayer</NavLink>
                <NavLink to='/apidoc/vector/Drawing'>easy/vector/Drawing</NavLink>
                <NavLink to='/apidoc/style/Style'>easy/style/Style</NavLink>
            </nav>
        </div>
        <div className={classnames('p-4', main)}>
            {children}
        </div>
    </DefaultLayout>
);