import React from 'react';
import { highlighted } from './Highlighted.module.css';

export default ({ name, type, defaultValue, children }) => (
    <tr>
        <td><label className={highlighted}>{name}</label></td>
        <td className='text-muted'>{type}</td>
        {defaultValue && <td>{defaultValue}</td>}
        <td>{children}</td>
    </tr>
);