import React from 'react';
import Link from './Link';


const COORDINATE_TYPE = 'coordinate',
    MAP_TYPE = 'map',
    FEATURE_TYPE = 'feature',
    EXTENT_TYPE = 'extent',
    DRAW_EVENT_TYPE = 'draw_event',
    CHANGE_EVENT_TYPE = 'change_event',
    PROJECTION_TYPE = 'projection',
    STYLE_TYPE = 'style',
    TILE_LAYER_TYPE = 'tile_layer';

const map = {
    [COORDINATE_TYPE]: {
        name: 'module:ol/coordinate~Coordinate',
        href: 'https://openlayers.org/en/latest/apidoc/module-ol_coordinate.html#~Coordinate'
    },
    [MAP_TYPE]: {
        name: 'ol/Map~Map',
        href: 'https://openlayers.org/en/latest/apidoc/module-ol_Map-Map.html'
    },
    [FEATURE_TYPE]: {
        name: 'module:ol/Feature~Feature',
        href: 'https://openlayers.org/en/latest/apidoc/module-ol_Feature-Feature.html'
    },
    [EXTENT_TYPE]: {
        name: 'module:ol/extent~Extent',
        href: 'https://openlayers.org/en/latest/apidoc/module-ol_extent.html#~Extent'
    },
    [DRAW_EVENT_TYPE]: {
        name: 'module:ol/interaction/Draw~DrawEvent',
        href: 'https://openlayers.org/en/latest/apidoc/module-ol_interaction_Draw-DrawEvent.html'
    },
    [CHANGE_EVENT_TYPE]: {
        name: 'module:ol/events/Event~Event',
        href: 'https://openlayers.org/en/latest/apidoc/module-ol_events_Event-Event.html'
    },
    [PROJECTION_TYPE]: {
        name: 'module:ol/proj~ProjectionLike',
        href: 'https://openlayers.org/en/latest/apidoc/module-ol_proj.html#~ProjectionLike'
    },
    [STYLE_TYPE]: {
        name: 'module:ol/style/Style~Style',
        href: 'https://openlayers.org/en/latest/apidoc/module-ol_style_Style-Style.html'
    },
    [TILE_LAYER_TYPE]: {
        name: 'module:ol/layer/Tile~TileLayer',
        href: 'https://openlayers.org/en/latest/apidoc/module-ol_layer_Tile-TileLayer.html'
    }
};

const Type = ({ name, href, array }) => (
    <React.Fragment>
        {array && 'Array.<'}
        <Link href={href} >{name}</Link>
        {array && '>'}
    </React.Fragment>
);

export const CoordinateType = props => <Type {...map[COORDINATE_TYPE]} {...props} />
export const MapType = props => <Type {...map[MAP_TYPE]} {...props} />
export const FeatureType = props => <Type {...map[FEATURE_TYPE]} {...props} />
export const ExtentType = props => <Type {...map[EXTENT_TYPE]} {...props} />
export const DrawEventType = props => <Type {...map[DRAW_EVENT_TYPE]} {...props} />
export const ChangeEventType = props => <Type {...map[CHANGE_EVENT_TYPE]} {...props} />
export const ProjectionType = props => <Type {...map[PROJECTION_TYPE]} {...props} />
export const StyleType = props => <Type {...map[STYLE_TYPE]} {...props} />
export const TileLayerType = props => <Type {...map[TILE_LAYER_TYPE]} {...props} />