import React from 'react';
import APILayout from '../../../layouts/APILayout';
import Section from '../../../modules/apidoc/components/Section';
import FunctionName from '../../../modules/apidoc/components/FunctionName';
import ParameterTable from '../../../modules/apidoc/components/ParameterTable';
import Parameter from '../../../modules/apidoc/components/Parameter';
import { MapType, TileLayerType, ProjectionType, FeatureType, CoordinateType, ExtentType } from '../../../modules/apidoc/components/OpenlayersDataTypes';
import ParameterGroup from '../../../modules/apidoc/components/ParameterGroup';
import Highlighted from '../../../modules/apidoc/components/Highlighted';
import FunctionDescription from '../../../modules/apidoc/components/FunctionDescription';
import Returns from '../../../modules/apidoc/components/Returns';
import Link from '../../../modules/apidoc/components/Link';
import SectionSubtitle from '../../../modules/apidoc/components/SectionSubtitle';

const ECQLLink = () => <Highlighted><Link href='https://docs.geoserver.org/stable/en/user/tutorials/cql/cql_tutorial.html'>ECQL</Link></Highlighted>;
const GeoserverWFSGetFeatureOperationDocLink = () => <Link href="https://docs.geoserver.org/stable/en/user/services/wfs/reference.html#getfeature">https://docs.geoserver.org/stable/en/user/services/wfs/reference.html#getfeature</Link>;
const GeoserverWMSGetMapOperationDocLink = () => <Link href="https://docs.geoserver.org/stable/en/user/services/wms/reference.html#getmap">https://docs.geoserver.org/stable/en/user/services/wms/reference.html#getmap</Link>;

export default () => (
    <APILayout>
        <h4 className='font-weight-bold mb-4'>easy/layer/GeoserverLayer</h4>
        <Section>
            <FunctionName parameters={['layerName', 'options']}>new GeoserverLayer</FunctionName>
            <ParameterTable>
                <Parameter name='layerName' type='string'>Layer's name.</Parameter>
                <ParameterGroup name='options' type='LayerWMS Options'>
                    <ParameterTable withDefaultColumn={true}>
                        <Parameter name='visible' type='boolean' defaultValue='true'>visibility</Parameter>
                        <Parameter name='url' type='string' defaultValue=' '>WMS service URL</Parameter>
                        <Parameter name='crossOrigin' type='boolean' defaultValue=' '>
                            The crossOrigin attribute for loaded images. Note that you must provide a crossOrigin value if you are using the WebGL renderer or if you want to access pixel data with the Canvas renderer. See <Link href='https://developer.mozilla.org/en-US/docs/Web/HTML/CORS_enabled_image'>https://developer.mozilla.org/en-US/docs/Web/HTML/CORS_enabled_image</Link> for more detail.
                        </Parameter>
                        <Parameter name='getMapParams' type='object' defaultValue=' '>
                            object containing WMS GetMap operation parameters. See <GeoserverWMSGetMapOperationDocLink />
                        </Parameter>
                    </ParameterTable>
                </ParameterGroup>
            </ParameterTable>
            <SectionSubtitle><strong>Extends</strong></SectionSubtitle>
            <ul>
                <li><TileLayerType/></li>
            </ul>
        </Section>
        <Section>
            <FunctionName>refresh</FunctionName>
            <FunctionDescription>Refresh the layer.</FunctionDescription>
        </Section>
        <Section>
            <FunctionName>filter</FunctionName>
            <FunctionDescription>Apply a filter to the layer using <ECQLLink /></FunctionDescription>
        </Section>
        <Section>
            <FunctionName>unfilter</FunctionName>
            <FunctionDescription>Remove the filter previously applied to the layer</FunctionDescription>
        </Section>
        <Section>
            <FunctionName returns='String'>getFilter</FunctionName>
            <FunctionDescription>Return the current filter applied to the layer</FunctionDescription>
            <Returns>ECQL filter</Returns>
        </Section>
        <Section>
            <FunctionName parameters={['style']}>setStyle</FunctionName>
            <FunctionDescription>Set the style for the layer</FunctionDescription>
            <ParameterTable>
                <Parameter name='style' type='string'>Name of the style to use. It may be empty to use the default layer styling.</Parameter>
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName returns='String'>getStyle</FunctionName>
            <FunctionDescription>Get the current style of the layer. An empty string means the layer is using its default style</FunctionDescription>
            <Returns>Name of the current style</Returns>
        </Section>
        <Section>
            <FunctionName returns='Promise' parameters={['cql_filter']}>countFeatures</FunctionName>
            <FunctionDescription>Get the number of features of a layer. It takes into account the filter of the layer to make the counting</FunctionDescription>
            <ParameterTable>
                <Parameter name='cql_filter' type='String'><ECQLLink /> filter. It will be used along with the filter of the layer to resolve the number of features.</Parameter>
            </ParameterTable>
            <Returns>Promise</Returns>
            <ParameterTable>
                <Parameter name='numerOfFeatures' type='number'>Number of features</Parameter>
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName returns='Promise' parameters={['getFeatureParams', 'featureProjection']}>getFeatures</FunctionName>
            <FunctionDescription>Get features of the layer using the WFS GetFeature operation</FunctionDescription>
            <ParameterTable>
                <Parameter name='getFeatureParams' type='object'>object containing WFS GetFeature operation parameters. See <GeoserverWFSGetFeatureOperationDocLink /></Parameter>
                <Parameter name='featureProjection' type={<ProjectionType />}>Projection the feature geometries must be in.</Parameter>
            </ParameterTable>
            <Returns>Promise</Returns>
            <ParameterTable>
                <Parameter name='features' type={<FeatureType array />}>Features.</Parameter>
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName returns='Promise' parameters={['coordinate', 'map']}>getFeaturesAtCoordinate</FunctionName>
            <FunctionDescription>Get all features whose geometry intersects the provided coordinate.</FunctionDescription>
            <ParameterTable>
                <Parameter name='coordinate' type={<CoordinateType />}>Coordinate.</Parameter>
                <Parameter name='map' type={<MapType />}>The Map.</Parameter>
            </ParameterTable>
            <Returns>Promise</Returns>
            <ParameterTable>
                <Parameter name='features' type={<FeatureType array />}>Features.</Parameter>
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName returns='Promise'>getAttributes</FunctionName>
            <FunctionDescription>Get the list of attributes of the layer.</FunctionDescription>
            <Returns>Promise</Returns>
            <ParameterTable>
                <Parameter name='attributes' type='object'>List of attributes.</Parameter>
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName returns='Promise'>getBBOX</FunctionName>
            <FunctionDescription>Get the bbox of the layer. It takes into account the filter of the layer to resolve the extent.</FunctionDescription>
            <Returns>Promise</Returns>
            <ParameterTable>
                <Parameter name='extent' type={<ExtentType />}>BBOX of the layer.</Parameter>
                <Parameter name='crs' type='string'>The CRS identifier of the extent.</Parameter>
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName parameters={['options']}>exportFeatures</FunctionName>
            <FunctionDescription>Export features of the layers</FunctionDescription>
            <ParameterTable>
                <ParameterGroup name='options' type='Export options'>
                    <ParameterTable withDefaultColumn={true}>
                        <Parameter name='format' type='string' defaultValue='excel'>format of the exported data</Parameter>
                        <Parameter name='dxfSpecificOptions' type='object' defaultValue=' '>Specific options for exporting in dxf format and only if the <Link href='https://docs.geoserver.org/stable/en/user/extensions/dxf/index.html'>DXF OutputFormat for WFS and WPS PPIO</Link> plugin is installed.</Parameter>
                        <Parameter name='getFeatureParams' type='object' defaultValue=' '>object containing WFS GetFeature operation parameters. See <GeoserverWFSGetFeatureOperationDocLink /></Parameter>
                    </ParameterTable>
                </ParameterGroup>
            </ParameterTable>
            <Returns>Features exported in the specified format</Returns>
        </Section>
        <Section>
            <FunctionName returns='Promise' parameters={['options']}>groupBy</FunctionName>
            <FunctionDescription>Perform a groupBy to the features of the layers</FunctionDescription>
            <ParameterTable>
                <ParameterGroup name='options' type='groupBy options'>
                    <ParameterTable>
                        <Parameter name='aggregationAttribute' type='string'>Attribute on which to perform aggregation</Parameter>
                        <Parameter name='aggregationFunctions' type='Array.<string>'>Aggregate functions to compute. Functions include Count, Average, Max, Median, Min, StdDev, and Sum</Parameter>
                        <Parameter name='groupByAttributes' type='Array.<string>'>List of group by attributes</Parameter>
                    </ParameterTable>
                </ParameterGroup>
            </ParameterTable>
            <Returns>Promise</Returns>
            <ParameterTable>
                <Parameter name='result' type='object'>Result of the groupBy opperation</Parameter>
            </ParameterTable>
        </Section>
    </APILayout>
);